<template>
  <div class="register">
    <Header></Header>
    <div class="box-main" v-if="index == '0'">
      <p>系统服务条款</p>
      <div class="box-content">
        为使用本服务，您应当阅读并遵守《服务协议》（以下简称“本协议”）和《隐私政策》。
        请您务必审慎阅读、充分理解各条款内容等，
        以及开通或使用某项服务的单独协议或规则，加下划线等形式提示您重点注意。
      </div>
      <br />
      <div class="box-content">
        除非您已充分阅读、完全理解并接受本协议所有条款，否则您无权使用本服务。
        您点击“同意”再点击“下一步”，均视为您已阅读并同意签署本协议。
        本协议即在您与腾讯之间产生法律效力，成为对双方均具有约束力的法律文件。
      </div>
      <br />
      <div class="box-list">
        <table>
          <thead>
            <tr>
              <td>可选方案</td>
              <td>内容介绍</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>方案一</td>
              <td>折价率8%，按天计价。</td>
            </tr>
            <tr>
              <td>方案二</td>
              <td>折价率5%，设备租用。</td>
            </tr>
            <tr>
              <td>方案三</td>
              <td>折价率3%，月结。</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="box-check" v-if="index == '0'">
      <mt-radio
        title="请选择上述其中一个方案"
        v-model="chooseValue"
        :options="options">
      </mt-radio>
    </div>
    <div class="box-check" v-if="index == '1'">
      <mt-field label="姓名：" placeholder="请输入姓名" v-model="Obj.username"></mt-field>
      <mt-field label="手机：" placeholder="请输入手机号码" type="tel" v-model="Obj.phone"></mt-field>
      <mt-field label="地址：" placeholder="请输入详细地址" type="textarea" rows="2" v-model="Obj.addr"></mt-field>
      <mt-field label="坐标：" placeholder="点击地图选取地理坐标" v-model="Obj.point" :disabled="allowInput"></mt-field>
      <el-amap vid="amap" class="amap" :center="center" :zoom="zoom" :plugin="plugin" :events="events">
        <el-amap-marker vid="amapDemo" :position="center"></el-amap-marker>
      </el-amap>
      <!--<mt-field label="备注说明" placeholder="申请内容" type="textarea" rows="3" v-model="introduction"></mt-field>-->
    </div>
    <div class="box-check" v-if="index == '2' || index == '3'">
      <input @change="getPhotoData" type="file" id="upload_file_front" ref="upload_file_front" style="display: none" accept="image/*" :capture="capture"/>
      <input @change="getPhotoData" type="file" id="upload_file_back" ref="upload_file_back" style="display: none" accept="image/*" :capture="capture"/>
      <input @change="getPhotoData" type="file" id="upload_file_other" ref="upload_file_other" style="display: none" accept="image/*" :capture="capture"/>
      <!--<mt-button type="primary" size="small" @click="chooseType">身份证图片</mt-button>-->
      <div class="id-card" @click="chooseType('front')">
        <div class="card">
          <span class="mintui mintui-account" v-if="uploadFront"></span>
          <div v-if="uploadFront">身份证正面照</div>
          <div class="showPhotoArea" v-if="!uploadFront">
            <img :src="srcImg_front"/>
          </div>
        </div>
      </div>
      <div class="id-card" @click="chooseType('back')">
        <div class="card">
          <span class="mintui mintui-account" v-if="uploadBack"></span>
          <div v-if="uploadBack">身份证反面照</div>
          <div class="showPhotoArea" v-if="!uploadBack">
            <img :src="srcImg_back"/>
          </div>
        </div>
      </div>
      <div class="id-card" @click="chooseType('other')">
        <div class="card">
          <span class="mintui mintui-image-fill" v-if="uploadOther"></span>
          <div v-if="uploadOther">门店牌正面照</div>
          <div class="showPhotoArea" v-if="!uploadOther">
            <img :src="srcImg_other"/>
          </div>
        </div>
      </div>
      <mt-actionsheet
        :actions="actions"
        v-model="Obj.sheetVisible">
      </mt-actionsheet>
    </div>
    <div class="box-footer">
      <mt-button type="primary" size="normal" @click="Next">下一步</mt-button>
      <mt-button type="danger" size="normal" @click="$router.push('/')">取消</mt-button>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import { Toast, Indicator } from 'mint-ui'
export default {
  name: 'Register',
  components: { Header },
  data () {
    var that = this
    return {
      index: 0,
      value: [],
      options: [
        {label: '方案一', value: '1'},
        {label: '方案二', value: '2'},
        {label: '方案三', value: '3'}
      ],
      chooseValue: '',
      capture: false,
      allowInput: true,
      Obj: {
        username: '',
        phone: '',
        addr: '',
        point: '',
        sheetVisible: false
      },
      actions: [
        {name: '拍照上传', method: this.getCamera	},
        {name: '相册选择', method: this.getLibrary}
      ],
      takeIds: '',
      uploadFront: true,
      uploadBack: true,
      uploadOther: true,
      srcImg_front: '',
      srcImg_back: '',
      srcImg_other: '',
      // 地图
      center: [114.03176, 22.52979],
      zoom: 16,
      events: {
        init: o => {
          o.getCity(result => {
            console.log(result)
          })
        },
        'click': (e) => {
          // 点击地图的时候，获取点击的经纬度，并将地图中心点移自此处
          let m = e.lnglat
          that.center = [m.lng, m.lat]
          that.Obj.point = '[' + m.lng + ',' + m.lat + ']'
          // 获取具体地址名称
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: 'all'
          })
          geocoder.getAddress(that.center, function (status, result) {
            if(status === 'complete' && result.info === 'OK') {
              that.Obj.addr = result.regeocode.formattedAddress
            }
          })
        }
      },
      plugin: [{
        // 获取当前位置，只能支持https访问获取
        pName: 'Geolocation',
        events: {
          init(o) {
            // o 是高德地图定位插件实例
            o.getCurrentPosition((status, result) => {
              if (result && result.position) {
                that.center = [result.position.lng, result.position.lat]
              }
            })
          }
        }
      }],
    }
  },
  methods: {
    getPhotoData(file) {
      if(this.takeIds === 'front') {
        // 身份证正面
        this.uploadFront = false
      }
      if(this.takeIds === 'back') {
        // 身份证反面
        this.uploadBack = false
      }
      if(this.takeIds === 'other') {
        // 门面牌照
        this.uploadOther = false
      }
      var reader = new FileReader()
      reader.readAsDataURL(file.target.files[0])
      // 这里必须要重定向
      let that = this
      reader.onload = function (e) {
        if(that.takeIds === 'front') {
          // 身份证正面
          that.srcImg_front = e.target.result
        }
        if(that.takeIds === 'back') {
          // 身份证反面
          that.srcImg_back = e.target.result
        }
        if(that.takeIds === 'other') {
          // 身份证反面
          that.srcImg_other = e.target.result
        }
      }
    },
    Next() {
      if(this.chooseValue != '') {
        this.index++
        if(this.index === 3) {
          Indicator.open('处理中...')
          setTimeout(() => {
            Toast({
              message: '申请提交成功',
              position: 'bottom'
            })
            sessionStorage.setItem('_mis', '1')
            Indicator.close()
            this.$router.push('/')
          }, 3000)
        } else {
        }
      } else {
        Toast({
          message: '请先选择一个方案',
          iconClass: ''
        })
      }
    },
    getCamera() {
      // 调用相机
      this.capture = true
      setTimeout((v) => {
        document.getElementById('upload_file_' + this.takeIds).click()
      }, 500)
    },
    getLibrary() {
      // 调用相册
      this.capture = false
      setTimeout((v) => {
        document.getElementById('upload_file_' + this.takeIds).click()
      }, 500)
    },
    chooseType(id) {
      this.takeIds = id
      this.Obj.sheetVisible = true
    }
  },
  mounted() {
  }
}
</script>
<style scoped>
  .box-main{
    border: 1px solid #d2d2d2;
    margin: 15px 2%;
    padding: 0px 10px 10px 10px;
  }
  .box-content {
    text-align: left;
    text-indent: 2em;
  }
  .box-check {
    text-align: left;
  }
  .box-footer {
    margin: 20px 0px 50px;
  }
  .box-list table {
    width: 100%;
    border-collapse: collapse;
    text-align: left;
  }
  .box-list table tr td {
    border: 1px solid #ff915f;
    padding: 8px 10px;
  }
  .box-list table thead tr td{
    padding: 15px 10px;
  }
  .id-card{
    width: 94%;
    margin-left: 3%;
  }
  .showPhotoArea img{
    height: 230px;
    width: 96%;
    margin-left: 2%;
    margin-top: 5px;
    border-radius: 10px;
  }
  .card{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 240px;
    border-radius: 10px;
    margin: 20px 0px 0px;
    border: 1px dashed #d2d2d2;
    user-select: none;
  }
  .card span {
    line-height: 240px;
    font-size: 2em;
    color: #d2d2d2;
  }
  .card div{
    line-height: 240px;
    font-size: 1.5em;
    color: #d2d2d2;
  }
  .amap{
    height: 350px;
  }
</style>



